  text-align: center;
  border-radius: 6px;
  padding: 3px 0;
  position: absolute;
  margin-top: calc(-26px - 1rem);
  z-index: 5000;
  border: 1px solid #c4c4c4;
  display: flex;
  align-items: center;
}

.popup > span {
  padding: 0 4px;
}

