  background: azure;
}

.fade-enter-active, .fade-leave-active {
  transition: opacity .1s;
}

.fade-enter, .fade-leave-to {
  opacity: 0;
}

.btn-md {
  display: flex;
  align-items: center;
  padding: 0 2px !important;
}

.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
