  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  display: table;
}

.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.selected {
  background-color: #ffffff !important;
  border: 2px #bebebe solid !important;
